import { ChangeDetectorRef, Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { Location } from '@angular/common';
import { Subscription } from 'rxjs';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { BranchService } from './kyc-branch.service';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormArray,
  FormControl,
} from '@angular/forms';
import { Router } from '@angular/router';
import { DuplicationComponent } from 'src/app/components/duplication/duplication.component';
import { OtpVerificationPopup } from 'src/app/components/otp-verification-popup/otp-verification-popup.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { RekycPopupComponent } from 'src/app/components/rekyc-popup/rekyc-popup.component';
import { VerificationDoneComponent } from 'src/app/components/verification-done/verification-done.component';
import { CaptureImageComponent } from 'src/app/components/capture-image/capture-image.component';
import { TokenV2Page } from '../../token-v2/token-v2.page';
import { StaticDataService } from 'src/app/services/static-data-service';
import { VerificationModalComponent } from 'src/app/components/verification-modal/verification-modal.component';

@Component({
  selector: 'app-kyc-branch',
  templateUrl: './kyc-branch.page.html',
  styleUrls: ['./kyc-branch.page.scss'],
})
export class KycBranchPage implements OnInit {
  documentUploadForm: FormGroup;
  @Input() retainValues;

  // svg icons links
  deleteIcon = 'assets/icon/delete.svg';
  editIcon = 'assets/icon/edit.svg';
  faceScanIcon = 'assets/icon/faceScan.svg';
  fingerScanIcon = 'assets/icon/fingerPrintScan.svg';
  irisScanIcon = 'assets/icon/irisScan.svg';

  // selected value of segment tab
  selectedTab: string = 'BASIC-INFO';
  previousTab: string;
  selectedOptionValue;

  percentDone: number;
  uploadSuccess: boolean;
  isUploading: boolean = false;
  uploadingFile: string;
  requestSubscription: Subscription;
  selectedIndex: number;

  prefixSelectedValue;
  genderSelectedValue;
  maritalStatusSelectedValue;
  nationalityStatusSelectedValue;
  resTypeSelectedValue;
  countrySelectedValue;
  stateSelectedValue;
  citySelectedValue;
  basicInfoForm: FormGroup;

  uploadSubscribe: any;

  documentForm: FormGroup;

  documentButtonDisabled = true;
  verificationButtonDisabled = true;

  permanentAddress: FormGroup;
  verificationForm: FormGroup;
  customerId: any;
  selectedFiles: any;
  image: any;
  mobileVerified: boolean = false;
  emailVerified: boolean = false;
  selectedData: any;
  getCapturedImageRes: any;
  irisImage: any;
  faceImage: any;
  basicInfoData: any;
  scannedItem: string;
  fingerPrintVerified: boolean = false;

  documentArr:string[];
  dispPhoneNumber: boolean;
  documentSide = [6,7,8]

  documentUploadData: any ={}
  verificationName: string;


  constructor(
    private fb: FormBuilder,
    private router: Router,
    private modalCtrl: ModalController,
    private _location: Location,
    private branchService: BranchService,
    private cdr: ChangeDetectorRef,
    private snack: MatSnackBar,
    private staticDataService: StaticDataService
  ) {}


  ngOnChanges(changes: SimpleChanges): void {
    if (this.retainValues && this.documentUploadForm) {
      this.documentUploadForm.patchValue(this.retainValues);
    }
  }

  ngOnInit() {
    this.buildDocumentUploadForm();
    this.buildVerificationForm();
    this.getDocumentName()
    this.permanentAddress = this.fb.group({});
  }

  ionViewWillEnter() {
    this.openDialog();
  }

  buildVerificationForm() {
    this.verificationForm = this.fb.group({
      irisScan: [''],
      faceScan: [''],
    });
  }

  closeModal() {
    this.modalCtrl.dismiss();
  }

  // DOCUMENTS

  buildDocumentUploadForm() {
    this.documentUploadForm = this.fb.group({
      documents: this.fb.array([this.createDocumentItem()]),
    });
    if (this.retainValues) {
      this.basicInfoForm.patchValue(this.retainValues);
    }
  }

  documents(): FormArray {
    return this.documentUploadForm.get('documents') as FormArray;
  }

  createDocumentItem() {
    return this.fb.group({
      documentName: ['', Validators.required],
      documentNumber: ['', Validators.required],
      phoneNumber: [''],
      verifiedMobileNumber: [''],
      pages: this.fb.array([this.addNewPage(0), this.addNewPage(1)]),
    });
  }

  addDocumentItem() {
    this.documents().push(this.createDocumentItem());
  }

  removeDocumentItem(index: number) {
    this.documents().removeAt(index);
  }

  documentPages(index: number): FormArray {
    return this.documents().at(index).get('pages') as FormArray;
  }

  getDocumentName(){
    this.staticDataService.documentType().subscribe(res=>{
      this.documentArr = res
      console.log(res)
    })
  }

  addNewPage(side: number): FormGroup {
    return this.fb.group({
      id: [],
      fileUrl: ['', Validators.required],
      documentSide: [side, Validators.required],
      documentTitle: [this.getPageName(side), Validators.required],
      scan: [false],
      isUploaded: [false],
    });
  }



  getPageName(side: number) {
    switch (side) {
      case 0:
        return 'Front';
      case 1:
        return 'Back';
      default:
        return 'Other';
    }
  }

  addDocumentPage(docIndex: number) {
    let pagesLength = this.documents().at(docIndex).get('pages') as FormArray;
    console.log(pagesLength);
    let count = pagesLength.length;

    this.documentPages(docIndex).push(this.addNewPage(count++));
  }

  removeDocumentPage(index: number, pageIndex: number) {
    if(this.documentPages(index).controls[pageIndex].get('documentTitle').value != 'Other'){
      this.documentPages(index).controls[pageIndex].get('fileUrl').setValue('');
      return;
    }
    this.documentPages(index).removeAt(pageIndex);

  }



  goToNextTab(selectedTab: string) {
    console.log(selectedTab);
    if (selectedTab === 'BASIC-INFO') {

      let payload = {
        phoneNumber: this.basicInfoForm.value.phoneNumber,
        primaryEmailAdress: this.basicInfoForm.value.primaryEmailAdress,
      };
      this.branchService.fetchCustomerDetails(payload).subscribe((res) => {
        console.log(res);
        if (res.data != null) {
          this.openDuplicationDiaglogue(res.data);
        } else {
          this.saveCustomerData();
        }
      });
    } else {
      this.selectedTab = 'VERIFICATION';
      this.verificationButtonDisabled = false;
    }
  }

  saveCustomerData() {
    let payload = this.basicInfoForm.value;
    payload.verificationType = 'KYC';
    console.log(payload);

    this.branchService.upsertCustomer(payload).subscribe((res) => {
      this.customerId = res.customerId;
      this.selectedTab = 'DOCUMENTS';

    });
  }

  goToPrevious(selectedTab: string) {
    this.selectedTab = selectedTab;
  }

  submit() {
    if(this.verificationName === 'FACE'){
      var payload = {
        verificationName:"FACE",
        id: null,
        fileName: 'scannedFace.jpg',
        biometricType:"FACE-SCAN",
        fpTemplateBase64: this.faceImage
      }
    }
    if(this.verificationName === 'IRIS'){
      var payload = {
        verificationName:"IRIS",
        id: null,
        fileName: 'scannedIris.jpg',
        biometricType:"IRIS-SCAN",
        fpTemplateBase64: this.irisImage
      }
    }
    this.branchService.uploadBiometric(payload).subscribe(res => {
      if(res){
        console.log(res)
        this.openSuccesfullScreen(res);
      }
    })
  }

    // this.branchService
    //   .updateKycStatusByCustId(this.customerId, 'INITIATED', '')
    //   .subscribe((res) => {
    //     if (res) {
    //       this.openSuccesfullScreen(res);
    //     }
    //   });


  getBasicInfo(event) {
    if (event) {
      console.log('event: ', event)
      this.documentButtonDisabled = false;
      this.basicInfoData = event;
      this.customerId = this.basicInfoData.data.customerId;
      console.log('From KYC Branch getBasicinfo(): ', this.basicInfoData.data.customerId)
      this.documentUploadData.customerId  = this.basicInfoData.data.customerId;

      if(this.basicInfoData.customerId){
        setTimeout(() => {
          this.getCustomerDocuments();
        }, 100);
      }
      this.selectedTab = 'DOCUMENTS';
    }
  }

  changeTab(event){
    if(event === true){
      this.selectedTab = 'VERIFICATION'
    }
  }

  async openSuccesfullScreen(data) {
    let modal = await this.modalCtrl.create({
      component: TokenV2Page,
      componentProps: {
        scheduleInfo: data,
        screenType: 'branch',
        screen: 'mobile-kyc',
        txntitle: 'KYC Branch Screening',
      },
    });

    modal.onDidDismiss().then((res) => {
      this.router.navigate(['kyc']);
    });

    return await modal.present();
  }

  async openDuplicationDiaglogue(customerData) {
    let modal = await this.modalCtrl.create({
      component: DuplicationComponent,
      backdropDismiss: true,
      cssClass: 'dup-modal',
      componentProps: {
        customerData: customerData,
      },
    });

    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
      if (res.data != 'close') {
        if (res.data.reKyc == true) {
          this.openSuccessDialog();
        } else {
          this.saveCustomerData();
        }
      }
    });

    return await modal.present();
  }
  async openSuccessDialog() {
    let modal = await this.modalCtrl.create({
      component: RekycPopupComponent,
    });

    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
      this.saveCustomerData();
    });

    return await modal.present();
  }

  async openDialog() {
    let modal = await this.modalCtrl.create({
      component: VerificationModalComponent,
      componentProps: {
        data: '',
        screen: 'branch',
      },
    });

    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
      if (res?.data?.customerData) {
        // this.basicInfoForm.patchValue(res?.data?.customerData);
        this.basicInfoData = res?.data?.customerData;
      }
    });

    return await modal.present();
  }
  patchUploadedDocument(data){
    if(data){
      data?.map((doc,index) => {
   this.documents().at(0).patchValue(doc);
   index == 0 ?
    this.documentPages(0)
    .at(0)
    .get('fileUrl')
    .patchValue(doc?.fileUrl):
    this.documentPages(0)
    .at(1)
    .get('fileUrl')
    .patchValue(doc?.fileUrl);
      })
    }
 }
  async openVerificationDone() {
    let modal = await this.modalCtrl.create({
      component: VerificationDoneComponent,
      componentProps: {},
    });
    modal.onDidDismiss().then(res => {
      this.fingerPrintVerified = true
    })
    return await modal.present();
  }

  async openCamera(scanType) {

    if (scanType == 'FACE') {
      this.verificationName = 'FACE'
      this.scannedItem = 'face'
    } else {
      this.verificationName = 'IRIS'
      this.scannedItem = 'iris'
    }
    let modal = await this.modalCtrl.create({
      component: CaptureImageComponent,
      componentProps: {
        scanObject: this.scannedItem
      },
    });

    modal.onDidDismiss().then((res) => {
      console.log('Getting Back open Camera Data', res);

      if (scanType == 'FACE') {
        this.faceImage = res.data;
        this.scannedItem = 'face'
      } else {
        this.irisImage = res.data;
        this.scannedItem = 'iris'
      }

      const uploadData = new FormData();
      const file = this.DataURIToBlob(res.data);
      uploadData.append('file', file, 'image.jpg');
      uploadData.append('figName', scanType);
      uploadData.append('customerId', this.customerId);
      uploadData.append('verify', 'kyc-verification');
      this.branchService.saveOrUpdateBio(uploadData).subscribe((res: any) => {
        this.snack.open(` ${scanType} Saved Successfully!`, 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right',
        });
      });

      // console.log(this.verificationForm);
    });

    return await modal.present();
  }


  onFileSelect(
    e: any,
    docside,
    documentIndex: number,
    index: number,
    documentName
  ) {
    console.log('index', index);
    this.selectedIndex = index;
    const file = e.target.files[0];
    const fReader = new FileReader();
    fReader.readAsDataURL(file);
    fReader.onloadend = (_event: any) => {
      let filename = file.name;
      let base64File = _event.target.result;
      // this.uploadDoc(base64File);
      this.documentPages(documentIndex)
        .at(index)
        .get('fileUrl')
        .patchValue(base64File);
      this.fileUpload(file, documentIndex, index, base64File);
      console.log(filename);
    };
    this.selectedFiles = e.target.files[0];
    let size = e.target.files[0].size;
    this.shotToast(`Your file size is ${this.formatBytes(size, 2)}`);
    if (size > 1048576) {
      this.shotToast(
        `Your file size is ${this.formatBytes(
          size,
          2
        )}, Kindly Upload below ${this.formatBytes(1048576, 2)} file size`
      );
    }
    var reader = new FileReader();
    reader.readAsDataURL(e.target.files[0]);
    reader.onload = (event: any) => {
      this.image = reader.result;
    };

    let docType;
    const uploadData = new FormData();
    if (documentName === 'Aadhar card') {
      docType = '6';
    } else if (documentName === 'Pan card') {
      docType = '7';
    } else if (documentName === 'Passport') {
      docType = '8';
    }
    const data = {
      customerId: this.customerId
        ? this.customerId
        : localStorage.getItem('customer_id'),
      documentName: this.documents().at(documentIndex).get('documentName').value,
      documentType: docType,
      fileType: this.selectedFiles.type,
      fileName: this.selectedFiles.name,
      verificationType: 'KYC verification',
      documentSide: this.documentPages(documentIndex).at(index).get('documentSide').value + 1,
      idNumber: '',
      name: '',
      dob: '',
    };
    uploadData.append('data', JSON.stringify(data));
    uploadData.append('file', this.selectedFiles);
    this.branchService.upload(uploadData).subscribe((res) => {
      console.log(res);
    });
  }
  DataURIToBlob(dataURI: string) {
    const splitDataURI = dataURI.split(',');
    const byteString =
      splitDataURI[0].indexOf('base64') >= 0
        ? atob(splitDataURI[1])
        : decodeURI(splitDataURI[1]);
    const mimeString = splitDataURI[0].split(':')[1].split(';')[0];

    const ia = new Uint8Array(byteString.length);
    for (let i = 0; i < byteString.length; i++)
      ia[i] = byteString.charCodeAt(i);

    return new Blob([ia], { type: mimeString });
  }
  public formatBytes(bytes, decimals) {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }
  fileUpload(file, documentIndex, index, base64File) {
    this.uploadingFile = file.name;
    this.isUploading = true;
    this.requestSubscription = this.branchService
      .uploadAndProgress(file)
      .subscribe((event: any) => {
        if (event.type === HttpEventType.UploadProgress) {
          this.percentDone = Math.round((100 * event.loaded) / event.total);
        } else if (event instanceof HttpResponse) {
          this.uploadSuccess = true;
          this.percentDone = 0;
          this.isUploading = false;
          console.log(this.documents());
          this.documentPages(documentIndex)
            .at(index)
            .get('fileUrl')
            .patchValue(base64File);
          this.cdr.markForCheck();
        }
      });
  }

  // for Document upload

  shotToast(msg: string) {
    this.snack.open(`${msg}`, 'OK', {
      duration: 6000,
      verticalPosition: 'top',
      horizontalPosition: 'right',
    });
  }

  //----------__________----------__________----------__________----------__________
  backToDashboard() {
    this.router.navigate(['dashboard']);
  }

  goBack() {
    this._location.back();
  }

  async openOtpModal(source_key, source_value) {
    let modal = await this.modalCtrl.create({
      component: OtpVerificationPopup,
      componentProps: {
        Mnumber: source_value,
        screen: 'branch',
      },
    });

    modal.onDidDismiss().then((res) => {
      console.log('Getting Back Data', res);
      if (res?.data?.otp) {
        if (source_key == 'mobile') {
          this.mobileVerified = true;
        } else {
          this.emailVerified = true;
        }
      }
    });

    return await modal.present();
  }
  getOtp(source_key, source_value) {
    let payload = {
      source: 'customer',
      source_key: source_key,
      source_value: source_value,
    };
    this.branchService.getOtp(payload).subscribe((res) => {
      this.openOtpModal(source_key, source_value);
    });
  }

  getCustomerDocuments(){
    this.documentSide.map(doc => {
      this.branchService.fetchCustomerDocuments(this.basicInfoData.customerId,doc).subscribe((res:any) => {
        // console.log(res);
        if(res){
          let frontArray = res.filter(r => (r.verificationType = 'KYC verification' && r?.documentSide == 1 ));
          let backArray = res.filter(r => (r.verificationType = 'KYC verification' && r?.documentSide == 2 ));
          this.patchUploadedDocument([frontArray.at(-1),backArray.at(-1)])
        }
      })
    })
  }
}
